import React from "react";
import Fade from "react-reveal/Fade";
import { toast } from "react-toastify";

import { BiLock, BiCheckSquare, BiRocket, GiPlatform } from "react-icons/all";
import { Col, Row } from "react-bootstrap";

import PropTypes from "prop-types";

export default function FeatureCard({ feature }) {
  return (
    <Fade bottom cascade>
      <section className="feature-card">
        <div className="feature-card-image">
          <feature.picture size={100} />
        </div>
        <div className="feature-card-header"> {feature.feature} </div>

        <div className="feature-card-body">
          <p className="feature-description">
            {feature.description.split("/\n/g")}
          </p>
        </div>
        
        <div className="feature-card-footer">
          {feature.btn_1 !== "AUDIT" && (
            feature.btn_1==='MEDIUM'? (
              <button className="card-button-small" onClick={() => toast("Coming Soon...")}>{feature.btn_1}</button>
            ):
            (<button className="card-button-small" onClick={()=> document.getElementById('tiersystem').scrollIntoView()} >{feature.btn_1}</button>)
          )}
        </div>
      </section>
    </Fade>
  );
}

FeatureCard.propTypes = {
  feature: PropTypes.object.isRequired,
};
